                      <li>Para la "Modalidad B1”, se establecen desde uno (1) hasta cinco (5) plazas por especialidad convocada.</li>
                      <!-- <li>En caso de no haberse completado el número de plazas establecidas para esta modalidad, y luego de haberse agotado los plazos y procedimientos, se podrán completar las mismas con postulantes bachilleres que hayan cursado el Nivel de Educación Primaria Comunitaria Vocacional o su equivalente en Unidades Educativas o Centro de Educación Alternativa Fiscales o de Convenio en área Rural y/o Urbana que se encuentren ubicadas en poblaciones alejadas y/o de difi´cil acceso, con menor accesibilidad a servicios básicos, comunicación y otros, y Educación Secundaria Comunitaria Productiva o su equivalente en Unidades Educativas o Centro de Educación Alternativa Fiscales o de Convenio en área rural o urbana.</li> -->
