                    </ul>
                  </p>
                  <p class="mt-2" v-if="item.sigla == 'B5'">
                    <ul>
                      <li>En el marco del D.S. 4887 de fecha 8 de marzo de 2023, en el cual se declara el "2023 AÑO DE LA JUVENTUD HACIA EL BICENTENARIO", podrán postular solo por la presente gestión las y los bachilleres destacados con Excelencia Académica del Sistema Educativo Plurinacional, que hayan estudiado en el Nivel de Educación Secundaria Comunitaria Productiva en Unidades Educativas o Centros de Educación Alternativa, Fiscales o de Convenio y hayan obtenido promedio de calificación de 98 a 100 puntos en 5to y 6to año de escolaridad en la gestión 2022 y 2023 respectivamente.</li>
