                  </p>
                </div>
                <div v-if="item.sigla == 'A'">
                  <strong>Costo de Postulación:</strong>
                  <p class="mt-2">
                    <ul>
                      <li>El costo de postulación para esta modalidad es de <b>Bs. 150.00 (CIENTO CINCUENTA 00/100 BOLIVIANOS)</b>, que deberán ser depositados a la <b>Cuenta Corriente Fiscal No. 10000004669343 "Ministerio de Educación - Recursos Propios" en el BANCO UNIÓN S.A.</b></li>
                    </ul>
                  </p>
                </div>
                <div class="text-center">
                  <v-btn
                    type="submit"
                    color="primary"
                    @click="selectModalidad(item.id, item.sigla)"
                  >
                    Postular
