            </v-card>
          </v-col>
        </v-row>
      </div>
      <div v-else>
        <notify-card :title="notify_title" :load="notify_load" process="el proceso de admisión"></notify-card>
      </div>
    </v-container>
  </div>
</template>

<script>
  import HeaderTitle from "@/components/HeaderTitle";
  import NotifyCard from "@/components/NotifyCard";
  import Service from "../utils/general";
  import axios from "axios";

  export default {
    name: "admision-inscripcion",
    components: {
      HeaderTitle,
      NotifyCard,
    },
    data: () => ({
      notify_title: "Cargando",
      notify_load: true,
      convocatoria: {
        id: null,
        cite: ""
      },
      modalidades: [],
    }),
    mounted() {
      this.getModalidades();
      Service.removeModality();
    },
    methods: {
      getModalidades() {
        this.notify_load = true;
        axios
          .get(
            Service.getUrlAPI() + "admision/modalidad",
            Service.getHeader()
          )
          .then(response => {
            this.convocatoria = response.data.convocatoria ? response.data.convocatoria : { id: null, cite: "" };
            this.modalidades = response.data.modalidades;
            this.notify_title = "Notificación";
            this.notify_load = false;
          })
          .catch(() => {
            this.notify_load = false;
            console.error("Parametros no encontrado");
          });
      },

      selectModalidad(id, sigla) {
        Service.setModality(this.convocatoria.id+'|'+id+'|'+sigla);
        if (sigla === 'A') {
          this.$router.push({ name: "admision-registro-a" });
        } else {
          this.$router.push({ name: "admision-registro-b" });
        }
      },
    },
  };
</script>